import * as types from './actionTypes';
import Calculator from '../calculator';

const initialState = Calculator();

function Send(action) {  
  window.gtag('event', 'CHANGED_BY_USER', {
    'event_category': 'calc',
    'event_label': action.name +':'+ action.value,
  });
  timerID = undefined;
}
// if fast change one field, then reshedule with new action
let timerID;
let delayedName;

function SendWithDelay(action) {
  if (delayedName === action.name) {
    clearTimeout(timerID);
  }
  delayedName = action.name;
  timerID = setTimeout(Send, 5000, action);
}

export function calc(state = initialState, action = {}) {
  switch (action.type) {
    case types.CHANGED_BY_USER:
        SendWithDelay(action);
        return Calculator( { ...state, [action.name]: action.value } );
    default:
      return state;
  }
}