import React from 'react';
import {AppBar, Toolbar, Typography} from '@material-ui/core'
import GitHubForkRibbon from 'react-github-fork-ribbon';

import {withRouter, Link} from 'react-router-dom';

import {Button} from '@material-ui/core';

const Head = withRouter(props => <HeadWithoutRouter {...props}/>);

//https://stackoverflow.com/questions/50290408/how-to-use-react-ga-with-hashrouter
function trackPage(screen_name) {
  window.gtag('event', 'screen_view', { screen_name });
}

class LinkButton extends React.Component {
  render() {
    const pathname = this.props.location.pathname === "/" ? "/calculator" : this.props.location.pathname;
    return (
      <Link to={this.props.to} style={{ textDecoration: 'none' }}>
        <Button variant="contained"
          color={ pathname === this.props.to ? "secondary" : undefined }
          style ={{margin:1}}>{this.props.text}</Button>
      </Link>
    )
  }
}

class HeadWithoutRouter extends React.Component {
  componentDidMount() {
    const page = this.props.location.pathname;
    trackPage(page);
  }

  componentWillReceiveProps(nextProps) {
    const currentPage = this.props.location.pathname;
    const nextPage = nextProps.location.pathname;

    if (currentPage !== nextPage) {
      trackPage(nextPage);
    }
  }

  render() {
    return (      
      <div>
        <AppBar position="static">
          <Toolbar>
            <Typography variant="h6">
              Calculation of the Potential Profitability of Ethereum 2.0 Staking
            </Typography>          
          </Toolbar>          

          <Toolbar>
            <LinkButton to="/calculator" text="Reward Calculator"    location={this.props.location} />
            <LinkButton to="/pool"       text="Staking Pool"         location={this.props.location} />
{/*}            <LinkButton to="/forecast"   text="Launch Forecast"      location={this.props.location} />
            <LinkButton to="/monitoring" text="Validator Monitoring" location={this.props.location} />
    */}          </Toolbar>
        </AppBar>
        
        <GitHubForkRibbon position="right" 
                color="green"
                href="//github.com/skillful-alex/eth2stake"
                target="_blank" > 
                    Fork me on GitHub 
        </GitHubForkRibbon> 
      </div>
    );
  }
}

export default Head;
